.headerContainer {
  width: 100%;
  height: auto;
  background-color: #1e1e1e;
}

.headerTop {
  padding-top: 30px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0 20px;
}

.details {
  margin-top: 20px;
}

.headerBottom {
  padding-top: 20px;
  color: white;
}

.HeaderImage {
  height: 170px;
  width: 170px;
  border: 5px solid rgb(50, 88, 160);
  border-radius: 50%;
}

a {
  text-decoration: none;
  color: rgb(34, 130, 194);
}

